* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.background {
  width: 100%;
  height: 100vh;
  background-image: url("tokyobg.481b107f.jpg");
  background-position: center;
  background-size: cover;
}

.back-home {
  z-index: 100;
  position: absolute;
  top: 5px;
  left: 5px;
}

.back-home a {
  color: #fff;
  padding: 5px;
  text-decoration: none;
}

.home {
  width: 75%;
  height: 75%;
  background-image: url("tokyo.7839c760.jpg");
  background-position: center;
  background-size: cover;
  border-radius: 10px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.content {
  width: 700px;
  flex-direction: column;
  padding: 100px 0;
  display: flex;
}

.content h2 {
  text-align: center;
  color: #fff;
  letter-spacing: 10px;
  font-size: 52px;
}

.content h3 {
  text-align: center;
  color: #fff;
  font-size: 40px;
}

.content p {
  text-align: center;
  color: #fff;
  font-size: 30px;
}

.feedback-form {
  width: 450px;
  -webkit-backdrop-filter: blur(4px);
  backdrop-filter: blur(4px);
  padding: 50px 30px;
  position: relative;
}

label {
  color: #fff;
  font-size: 20px;
}

input {
  height: 30px;
  width: 100%;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: 3px solid #fff;
  outline: rgba(0, 0, 0, 0);
  font-size: 20px;
}

textarea {
  resize: none;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 3px solid #fff;
  border-top: none;
  border-left: none;
  outline: rgba(0, 0, 0, 0);
  font-size: 20px;
}

button {
  width: 100%;
  height: 40px;
  color: #212121;
  background-color: #fff;
  border: none;
  border-radius: 7px;
  outline: none;
  font-size: 20px;
  font-weight: 700;
}

button:hover {
  background-color: #efeeee;
}

.feedback-form h2 {
  text-align: center;
  color: #fff;
  font-size: 40px;
}

::-ms-input-placeholder {
  color: #fff;
  font-size: 18px;
}

::placeholder {
  color: #fff;
  font-size: 18px;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

